import { SampleData, Alert } from './types';
import { LOCATIONS } from './constants';

// Generate realistic sample data for each location
export function generateSampleData(): SampleData[] {
  const sampleData: SampleData[] = [];
  const operators = ['John Smith', 'Maria Garcia', 'David Chen', 'Sarah Johnson', 'Mike Wilson'];
  const methods: ('MANUAL' | 'BARCODE' | 'UPLOAD')[] = ['MANUAL', 'BARCODE', 'UPLOAD'];

  LOCATIONS.forEach((location) => {
    for (let i = 0; i < 20; i++) {
      const date = new Date();
      date.setDate(date.getDate() - Math.floor(Math.random() * 30)); // Last 30 days
      
      // Generate realistic values based on target
      let value: number;
      let status: 'PASS' | 'FAIL' | 'WARNING';
      
      const randomFactor = Math.random();
      if (randomFactor < 0.7) {
        // 70% chance of passing values
        value = location.targetValue + (Math.random() * 2 - 0.5); // 0.5% variation
        status = 'PASS';
      } else if (randomFactor < 0.9) {
        // 20% chance of warning values
        value = location.targetValue - (Math.random() * 2 + 0.5); // 0.5-2.5% below target
        status = 'WARNING';
      } else {
        // 10% chance of failing values
        value = location.targetValue - (Math.random() * 5 + 2.5); // 2.5-7.5% below target
        status = 'FAIL';
      }

      // Ensure values are within realistic bounds
      if (location.parameter === 'Cu' || location.parameter === 'H2SO4' || location.parameter === 'Au' || location.parameter === 'Ag') {
        value = Math.max(0, Math.min(100, value));
      }

      sampleData.push({
        id: `${location.id}-${i + 1}`,
        locationId: location.id,
        location: location.name,
        product: location.product,
        parameter: location.parameter,
        value: Number(value.toFixed(2)),
        unit: location.unit,
        target: location.targetValue,
        status,
        date: date.toISOString().split('T')[0],
        time: `${String(Math.floor(Math.random() * 24)).padStart(2, '0')}:${String(Math.floor(Math.random() * 60)).padStart(2, '0')}`,
        operator: operators[Math.floor(Math.random() * operators.length)],
        method: methods[Math.floor(Math.random() * methods.length)]
      });
    }
  });

  return sampleData.sort((a, b) => new Date(b.date + ' ' + b.time).getTime() - new Date(a.date + ' ' + a.time).getTime());
}

// Generate alerts based on sample data
export function generateAlerts(sampleData: SampleData[]): Alert[] {
  const alerts: Alert[] = [];
  
  // Get recent failing/warning samples
  const recentIssues = sampleData.filter(sample => 
    sample.status !== 'PASS' && 
    new Date(sample.date).getTime() > Date.now() - (7 * 24 * 60 * 60 * 1000) // Last 7 days
  );

  recentIssues.forEach((sample, index) => {
    const deviation = ((sample.target - sample.value) / sample.target) * 100;
    let severity: 'HIGH' | 'MEDIUM' | 'LOW';
    
    if (sample.status === 'FAIL') {
      severity = 'HIGH';
    } else if (deviation > 2) {
      severity = 'MEDIUM';
    } else {
      severity = 'LOW';
    }

    alerts.push({
      id: `alert-${index + 1}`,
      locationId: sample.locationId,
      location: sample.location,
      parameter: sample.parameter,
      currentValue: sample.value,
      targetValue: sample.target,
      deviation: Number(deviation.toFixed(2)),
      severity,
      timestamp: `${sample.date} ${sample.time}`,
      status: Math.random() > 0.3 ? 'ACTIVE' : 'RESOLVED'
    });
  });

  return alerts.sort((a, b) => new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime());
}

// Initialize data
export const SAMPLE_DATA = generateSampleData();
export const ALERTS_DATA = generateAlerts(SAMPLE_DATA);
