"use client";

import { useState, useMemo } from "react";
import DashboardLayout from "@/components/DashboardLayout";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { 
  History, 
  Calendar, 
  TrendingUp, 
  TrendingDown,
  Filter,
  Search,
  Clock,
  User,
  MapPin,
  Target
} from "lucide-react";
import { SAMPLE_DATA } from "@/lib/sampleData";
import { LOCATIONS } from "@/lib/constants";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, ScatterChart, Scatter } from 'recharts';

export default function HistoryPage() {
  const [selectedLocation, setSelectedLocation] = useState<string>("all");
  const [selectedParameter, setSelectedParameter] = useState<string>("all");
  const [dateRange, setDateRange] = useState<string>("30");
  const [searchTerm, setSearchTerm] = useState<string>("");
  const [selectedSample, setSelectedSample] = useState<any>(null);

  // Filter data based on selections
  const filteredData = useMemo(() => {
    let data = SAMPLE_DATA;
    
    if (selectedLocation !== "all") {
      data = data.filter(item => item.locationId === selectedLocation);
    }
    
    if (selectedParameter !== "all") {
      data = data.filter(item => item.parameter === selectedParameter);
    }
    
    if (searchTerm) {
      data = data.filter(item => 
        item.location.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.product.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.operator.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
    
    const daysAgo = parseInt(dateRange);
    const cutoffDate = new Date();
    cutoffDate.setDate(cutoffDate.getDate() - daysAgo);
    
    data = data.filter(item => new Date(item.date) >= cutoffDate);
    
    return data.sort((a, b) => new Date(b.date + ' ' + b.time).getTime() - new Date(a.date + ' ' + a.time).getTime());
  }, [selectedLocation, selectedParameter, dateRange, searchTerm]);

  // Prepare historical trend data
  const historicalTrend = useMemo(() => {
    const trendData = filteredData.map((sample, index) => ({
      ...sample,
      timestamp: new Date(sample.date + ' ' + sample.time).getTime(),
      index: filteredData.length - index
    })).sort((a, b) => a.timestamp - b.timestamp);

    return trendData;
  }, [filteredData]);

  // Get unique parameters
  const parameters = useMemo(() => {
    const uniqueParams = [...new Set(SAMPLE_DATA.map(item => item.parameter))];
    return uniqueParams;
  }, []);

  // Calculate statistics
  const statistics = useMemo(() => {
    if (filteredData.length === 0) return null;

    const values = filteredData.map(item => item.value);
    const avg = values.reduce((sum, val) => sum + val, 0) / values.length;
    const min = Math.min(...values);
    const max = Math.max(...values);
    
    // Calculate standard deviation
    const variance = values.reduce((sum, val) => sum + Math.pow(val - avg, 2), 0) / values.length;
    const stdDev = Math.sqrt(variance);

    const passed = filteredData.filter(item => item.status === 'PASS').length;
    const warnings = filteredData.filter(item => item.status === 'WARNING').length;
    const failed = filteredData.filter(item => item.status === 'FAIL').length;

    return {
      count: filteredData.length,
      average: Number(avg.toFixed(2)),
      minimum: Number(min.toFixed(2)),
      maximum: Number(max.toFixed(2)),
      standardDeviation: Number(stdDev.toFixed(2)),
      passed,
      warnings,
      failed,
      passRate: Number(((passed / filteredData.length) * 100).toFixed(1))
    };
  }, [filteredData]);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'PASS': return 'text-green-600 bg-green-50';
      case 'WARNING': return 'text-yellow-600 bg-yellow-50';
      case 'FAIL': return 'text-red-600 bg-red-50';
      default: return 'text-gray-600 bg-gray-50';
    }
  };

  const formatDateTime = (date: string, time: string) => {
    return new Date(date + ' ' + time).toLocaleString();
  };

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {/* Header */}
        <div>
          <h1 className="text-3xl font-bold text-gray-900">History</h1>
          <p className="text-gray-600 mt-1">Historical data analysis and trends</p>
        </div>

        {/* Filters */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <Filter className="h-5 w-5" />
              <span>Historical Filters</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <div className="space-y-2">
                <Label>Location</Label>
                <Select value={selectedLocation} onValueChange={setSelectedLocation}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select Location" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Locations</SelectItem>
                    {LOCATIONS.map(location => (
                      <SelectItem key={location.id} value={location.id}>
                        {location.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label>Parameter</Label>
                <Select value={selectedParameter} onValueChange={setSelectedParameter}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select Parameter" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Parameters</SelectItem>
                    {parameters.map(param => (
                      <SelectItem key={param} value={param}>
                        {param}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label>Time Period</Label>
                <Select value={dateRange} onValueChange={setDateRange}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="7">Last 7 Days</SelectItem>
                    <SelectItem value="30">Last 30 Days</SelectItem>
                    <SelectItem value="90">Last 90 Days</SelectItem>
                    <SelectItem value="180">Last 6 Months</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label>Search</Label>
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    placeholder="Search..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="pl-10"
                  />
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Statistics Summary */}
        {statistics && (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <Card>
              <CardContent className="p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Total Samples</p>
                    <p className="text-2xl font-bold">{statistics.count}</p>
                  </div>
                  <History className="h-8 w-8 text-blue-600" />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Pass Rate</p>
                    <p className="text-2xl font-bold text-green-600">{statistics.passRate}%</p>
                  </div>
                  <TrendingUp className="h-8 w-8 text-green-600" />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Average Value</p>
                    <p className="text-2xl font-bold">{statistics.average}</p>
                  </div>
                  <Target className="h-8 w-8 text-purple-600" />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-sm text-gray-600">Std Deviation</p>
                    <p className="text-2xl font-bold">{statistics.standardDeviation}</p>
                  </div>
                  <TrendingDown className="h-8 w-8 text-orange-600" />
                </div>
              </CardContent>
            </Card>
          </div>
        )}

        {/* Historical Trend Chart */}
        <Card>
          <CardHeader>
            <CardTitle>Historical Trend Analysis</CardTitle>
            <CardDescription>
              Quality measurements over time ({filteredData.length} samples)
            </CardDescription>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={400}>
              <LineChart data={historicalTrend}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis 
                  dataKey="date"
                  tickFormatter={(value) => new Date(value).toLocaleDateString()}
                />
                <YAxis />
                <Tooltip 
                  labelFormatter={(value) => new Date(value).toLocaleString()}
                  formatter={(value, name) => [
                    `${value}${historicalTrend[0]?.unit || ''}`, 
                    name === 'value' ? 'Measured Value' : 'Target'
                  ]}
                />
                <Line 
                  type="monotone" 
                  dataKey="target" 
                  stroke="#94A3B8" 
                  strokeDasharray="5 5"
                  name="Target"
                  dot={false}
                />
                <Line 
                  type="monotone" 
                  dataKey="value" 
                  stroke="#3B82F6" 
                  strokeWidth={2}
                  name="Actual"
                  dot={{ fill: '#3B82F6', strokeWidth: 2, r: 3 }}
                />
              </LineChart>
            </ResponsiveContainer>
          </CardContent>
        </Card>

        {/* Scatter Plot for Quality Distribution */}
        <Card>
          <CardHeader>
            <CardTitle>Quality Distribution Analysis</CardTitle>
            <CardDescription>
              Value distribution vs target comparison
            </CardDescription>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={300}>
              <ScatterChart data={historicalTrend}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis 
                  dataKey="index" 
                  name="Sample Order"
                  type="number"
                />
                <YAxis dataKey="value" name="Value" />
                <Tooltip 
                  cursor={{ strokeDasharray: '3 3' }}
                  formatter={(value, name) => [
                    `${value}${historicalTrend[0]?.unit || ''}`, 
                    'Value'
                  ]}
                  labelFormatter={(value) => `Sample #${value}`}
                />
                <Scatter 
                  dataKey="value" 
                  fill="#3B82F6"
                />
              </ScatterChart>
            </ResponsiveContainer>
          </CardContent>
        </Card>

        {/* Recent History Timeline */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <Clock className="h-5 w-5" />
              <span>Recent History Timeline</span>
            </CardTitle>
            <CardDescription>
              Latest {Math.min(20, filteredData.length)} samples
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {filteredData.slice(0, 20).map((sample, index) => (
                <div 
                  key={sample.id}
                  className="flex items-center space-x-4 p-4 border rounded-lg hover:bg-gray-50 cursor-pointer"
                  onClick={() => setSelectedSample(selectedSample?.id === sample.id ? null : sample)}
                >
                  <div className="flex-shrink-0">
                    <div className={`w-3 h-3 rounded-full ${
                      sample.status === 'PASS' ? 'bg-green-500' :
                      sample.status === 'WARNING' ? 'bg-yellow-500' : 'bg-red-500'
                    }`} />
                  </div>
                  
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-2">
                        <MapPin className="h-4 w-4 text-gray-400" />
                        <span className="font-medium text-sm">{sample.location}</span>
                        <Badge variant="outline" className="text-xs">{sample.parameter}</Badge>
                      </div>
                      <div className="flex items-center space-x-2">
                        <span className="font-mono text-sm">{sample.value}{sample.unit}</span>
                        <Badge className={getStatusColor(sample.status)}>
                          {sample.status}
                        </Badge>
                      </div>
                    </div>
                    
                    <div className="flex items-center justify-between mt-1">
                      <div className="flex items-center space-x-4 text-xs text-gray-500">
                        <div className="flex items-center space-x-1">
                          <Calendar className="h-3 w-3" />
                          <span>{formatDateTime(sample.date, sample.time)}</span>
                        </div>
                        <div className="flex items-center space-x-1">
                          <User className="h-3 w-3" />
                          <span>{sample.operator}</span>
                        </div>
                      </div>
                      <span className="text-xs text-gray-500">
                        Target: {sample.target}{sample.unit}
                      </span>
                    </div>

                    {/* Expanded Details */}
                    {selectedSample?.id === sample.id && (
                      <div className="mt-3 p-3 bg-gray-50 rounded border-t">
                        <div className="grid grid-cols-2 gap-4 text-sm">
                          <div>
                            <span className="font-medium">Product:</span> {sample.product}
                          </div>
                          <div>
                            <span className="font-medium">Method:</span> {sample.method}
                          </div>
                          <div>
                            <span className="font-medium">Deviation:</span> 
                            <span className={`ml-1 ${
                              sample.value >= sample.target ? 'text-green-600' : 'text-red-600'
                            }`}>
                              {sample.value >= sample.target ? '+' : ''}{(sample.value - sample.target).toFixed(2)}{sample.unit}
                            </span>
                          </div>
                          <div>
                            <span className="font-medium">% of Target:</span> 
                            <span className={`ml-1 ${
                              sample.value >= sample.target ? 'text-green-600' : 'text-red-600'
                            }`}>
                              {((sample.value / sample.target) * 100).toFixed(1)}%
                            </span>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </DashboardLayout>
  );
}
