"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { 
  LayoutDashboard, 
  FileText, 
  MapPin, 
  History, 
  AlertTriangle, 
  LogOut,
  Menu,
  X,
  User,
  Plus
} from "lucide-react";
import { DASHBOARD_ROUTES } from "@/lib/constants";
import Link from "next/link";
import { usePathname } from "next/navigation";
import DataInput from "./DataInput";
import { SampleData } from "@/lib/types";

interface DashboardLayoutProps {
  children: React.ReactNode;
}

export default function DashboardLayout({ children }: DashboardLayoutProps) {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [showDataInput, setShowDataInput] = useState(false);
  const pathname = usePathname();

  const handleLogout = () => {
    window.location.href = "/";
  };

  const handleDataSubmit = (data: Partial<SampleData>) => {
    // In a real application, this would send data to an API
    console.log("New sample data:", data);
    // For demo purposes, we'll just show a success message
    alert(`Sample data submitted successfully!\nLocation: ${data.location}\nValue: ${data.value}${data.unit}\nStatus: ${data.status}`);
  };

  const getIcon = (id: string) => {
    switch (id) {
      case 'overview': return <LayoutDashboard className="h-5 w-5" />;
      case 'reports': return <FileText className="h-5 w-5" />;
      case 'locations': return <MapPin className="h-5 w-5" />;
      case 'history': return <History className="h-5 w-5" />;
      case 'alerts': return <AlertTriangle className="h-5 w-5" />;
      default: return <LayoutDashboard className="h-5 w-5" />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="flex items-center justify-between px-4 py-3">
          <div className="flex items-center space-x-4">
            <Button
              variant="ghost"
              size="icon"
              onClick={() => setSidebarOpen(!sidebarOpen)}
              className="lg:hidden"
            >
              {sidebarOpen ? <X className="h-5 w-5" /> : <Menu className="h-5 w-5" />}
            </Button>
            <div className="flex items-center space-x-3">
              <div className="w-8 h-8 bg-gradient-to-br from-orange-500 to-red-600 rounded-lg flex items-center justify-center">
                <span className="text-white font-bold text-sm">SX</span>
              </div>
              <h1 className="text-xl font-bold text-gray-800">SmelterXplore</h1>
            </div>
          </div>
          <div className="flex items-center space-x-4">
            <Button
              onClick={() => setShowDataInput(true)}
              className="flex items-center space-x-2 bg-gradient-to-r from-orange-500 to-red-600 hover:from-orange-600 hover:to-red-700"
            >
              <Plus className="h-4 w-4" />
              <span>Add Data</span>
            </Button>
            <div className="flex items-center space-x-2 text-sm text-gray-600">
              <User className="h-4 w-4" />
              <span>kanghaji</span>
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={handleLogout}
              className="flex items-center space-x-2"
            >
              <LogOut className="h-4 w-4" />
              <span>Logout</span>
            </Button>
          </div>
        </div>
      </header>

      <div className="flex">
        {/* Sidebar */}
        <aside className={`
          fixed inset-y-0 left-0 z-50 w-64 bg-white shadow-lg transform transition-transform duration-300 ease-in-out
          lg:translate-x-0 lg:static lg:inset-0
          ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'}
        `}>
          <div className="flex flex-col h-full pt-16 lg:pt-0">
            <nav className="flex-1 px-4 py-6 space-y-2">
              {DASHBOARD_ROUTES.map((route) => {
                const isActive = pathname === route.path;
                return (
                  <Link
                    key={route.id}
                    href={route.path}
                    className={`
                      flex items-center space-x-3 px-4 py-3 rounded-lg transition-colors
                      ${isActive 
                        ? 'bg-gradient-to-r from-orange-500 to-red-600 text-white' 
                        : 'text-gray-700 hover:bg-gray-100'
                      }
                    `}
                    onClick={() => setSidebarOpen(false)}
                  >
                    {getIcon(route.id)}
                    <span className="font-medium">{route.name}</span>
                  </Link>
                );
              })}
            </nav>
          </div>
        </aside>

        {/* Overlay for mobile */}
        {sidebarOpen && (
          <div 
            className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
            onClick={() => setSidebarOpen(false)}
          />
        )}

        {/* Main Content */}
        <main className="flex-1 lg:ml-0">
          <div className="p-6">
            {children}
          </div>
        </main>
      </div>

      {/* Data Input Modal */}
      {showDataInput && (
        <DataInput
          onDataSubmit={handleDataSubmit}
          onClose={() => setShowDataInput(false)}
        />
      )}
    </div>
  );
}
