"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { 
  Plus, 
  Upload, 
  QrCode, 
  Keyboard, 
  CheckCircle, 
  AlertTriangle,
  FileText,
  Camera
} from "lucide-react";
import { LOCATIONS } from "@/lib/constants";
import { SampleData } from "@/lib/types";

interface DataInputProps {
  onDataSubmit: (data: Partial<SampleData>) => void;
  onClose: () => void;
}

export default function DataInput({ onDataSubmit, onClose }: DataInputProps) {
  const [selectedLocation, setSelectedLocation] = useState<string>("");
  const [formData, setFormData] = useState({
    value: "",
    operator: "kanghaji",
    notes: ""
  });
  const [activeTab, setActiveTab] = useState("manual");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState<'success' | 'error' | null>(null);
  const [barcodeInput, setBarcodeInput] = useState("");
  const [uploadedFile, setUploadedFile] = useState<File | null>(null);

  const selectedLocationData = LOCATIONS.find(loc => loc.id === selectedLocation);

  const handleManualSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedLocation || !formData.value) return;

    setIsSubmitting(true);
    
    try {
      const value = parseFloat(formData.value);
      const location = LOCATIONS.find(loc => loc.id === selectedLocation)!;
      
      let status: 'PASS' | 'WARNING' | 'FAIL';
      if (value >= location.targetValue) {
        status = 'PASS';
      } else if (value >= location.targetValue * 0.95) { // Within 5% of target
        status = 'WARNING';
      } else {
        status = 'FAIL';
      }

      const newSample: Partial<SampleData> = {
        locationId: selectedLocation,
        location: location.name,
        product: location.product,
        parameter: location.parameter,
        value: value,
        unit: location.unit,
        target: location.targetValue,
        status: status,
        date: new Date().toISOString().split('T')[0],
        time: new Date().toTimeString().split(' ')[0].substring(0, 5),
        operator: formData.operator,
        method: 'MANUAL'
      };

      onDataSubmit(newSample);
      setSubmitStatus('success');
      
      // Reset form
      setFormData({ value: "", operator: "kanghaji", notes: "" });
      setSelectedLocation("");
      
      setTimeout(() => {
        setSubmitStatus(null);
        onClose();
      }, 2000);
      
    } catch (error) {
      setSubmitStatus('error');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleBarcodeSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!barcodeInput.trim()) return;

    setIsSubmitting(true);
    
    try {
      // Simulate barcode processing
      // In a real application, this would decode the barcode and extract sample data
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Mock barcode data extraction
      const mockBarcodeData = {
        locationId: 'copper-electro-refining',
        value: 99.95,
        operator: 'kanghaji'
      };
      
      const location = LOCATIONS.find(loc => loc.id === mockBarcodeData.locationId)!;
      let status: 'PASS' | 'WARNING' | 'FAIL';
      if (mockBarcodeData.value >= location.targetValue) {
        status = 'PASS';
      } else if (mockBarcodeData.value >= location.targetValue * 0.95) {
        status = 'WARNING';
      } else {
        status = 'FAIL';
      }

      const newSample: Partial<SampleData> = {
        locationId: mockBarcodeData.locationId,
        location: location.name,
        product: location.product,
        parameter: location.parameter,
        value: mockBarcodeData.value,
        unit: location.unit,
        target: location.targetValue,
        status: status,
        date: new Date().toISOString().split('T')[0],
        time: new Date().toTimeString().split(' ')[0].substring(0, 5),
        operator: mockBarcodeData.operator,
        method: 'BARCODE'
      };

      onDataSubmit(newSample);
      setSubmitStatus('success');
      setBarcodeInput("");
      
      setTimeout(() => {
        setSubmitStatus(null);
        onClose();
      }, 2000);
      
    } catch (error) {
      setSubmitStatus('error');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleFileUpload = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!uploadedFile) return;

    setIsSubmitting(true);
    
    try {
      // Simulate file processing
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // Mock file data extraction
      const mockFileData = [
        { locationId: 'flash-smelting-furnace', value: 71.2 },
        { locationId: 'anode-furnace', value: 99.3 },
        { locationId: 'acid-plant', value: 98.7 }
      ];
      
      // Process multiple samples from file
      for (const data of mockFileData) {
        const location = LOCATIONS.find(loc => loc.id === data.locationId)!;
        let status: 'PASS' | 'WARNING' | 'FAIL';
        if (data.value >= location.targetValue) {
          status = 'PASS';
        } else if (data.value >= location.targetValue * 0.95) {
          status = 'WARNING';
        } else {
          status = 'FAIL';
        }

        const newSample: Partial<SampleData> = {
          locationId: data.locationId,
          location: location.name,
          product: location.product,
          parameter: location.parameter,
          value: data.value,
          unit: location.unit,
          target: location.targetValue,
          status: status,
          date: new Date().toISOString().split('T')[0],
          time: new Date().toTimeString().split(' ')[0].substring(0, 5),
          operator: 'kanghaji',
          method: 'UPLOAD'
        };

        onDataSubmit(newSample);
      }
      
      setSubmitStatus('success');
      setUploadedFile(null);
      
      setTimeout(() => {
        setSubmitStatus(null);
        onClose();
      }, 2000);
      
    } catch (error) {
      setSubmitStatus('error');
    } finally {
      setIsSubmitting(false);
    }
  };

  const startBarcodeScanner = () => {
    // In a real application, this would access the device camera
    // For demo purposes, we'll simulate a barcode scan
    setBarcodeInput("SAMPLE_" + Date.now());
    alert("Barcode scanner activated! (Demo: Sample barcode generated)");
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <Card className="w-full max-w-2xl max-h-[90vh] overflow-y-auto">
        <CardHeader>
          <CardTitle className="flex items-center space-x-2">
            <Plus className="h-6 w-6" />
            <span>Add Lab Analysis Data</span>
          </CardTitle>
          <CardDescription>
            Input new quality measurement data using manual entry, barcode scanning, or document upload
          </CardDescription>
        </CardHeader>
        <CardContent>
          {submitStatus && (
            <Alert className={`mb-4 ${submitStatus === 'success' ? 'border-green-200 bg-green-50' : 'border-red-200 bg-red-50'}`}>
              {submitStatus === 'success' ? (
                <CheckCircle className="h-4 w-4 text-green-600" />
              ) : (
                <AlertTriangle className="h-4 w-4 text-red-600" />
              )}
              <AlertDescription className={submitStatus === 'success' ? 'text-green-800' : 'text-red-800'}>
                {submitStatus === 'success' 
                  ? 'Data submitted successfully!' 
                  : 'Error submitting data. Please try again.'
                }
              </AlertDescription>
            </Alert>
          )}

          <Tabs value={activeTab} onValueChange={setActiveTab}>
            <TabsList className="grid w-full grid-cols-3">
              <TabsTrigger value="manual" className="flex items-center space-x-2">
                <Keyboard className="h-4 w-4" />
                <span>Manual Entry</span>
              </TabsTrigger>
              <TabsTrigger value="barcode" className="flex items-center space-x-2">
                <QrCode className="h-4 w-4" />
                <span>Barcode Scan</span>
              </TabsTrigger>
              <TabsTrigger value="upload" className="flex items-center space-x-2">
                <Upload className="h-4 w-4" />
                <span>Document Upload</span>
              </TabsTrigger>
            </TabsList>

            {/* Manual Entry Tab */}
            <TabsContent value="manual" className="space-y-4">
              <form onSubmit={handleManualSubmit} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="location">Processing Location</Label>
                  <Select value={selectedLocation} onValueChange={setSelectedLocation} required>
                    <SelectTrigger>
                      <SelectValue placeholder="Select processing location" />
                    </SelectTrigger>
                    <SelectContent>
                      {LOCATIONS.map(location => (
                        <SelectItem key={location.id} value={location.id}>
                          {location.name} - {location.product}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                {selectedLocationData && (
                  <div className="p-3 bg-blue-50 rounded-lg">
                    <div className="grid grid-cols-2 gap-2 text-sm">
                      <div><strong>Product:</strong> {selectedLocationData.product}</div>
                      <div><strong>Parameter:</strong> {selectedLocationData.parameter}</div>
                      <div><strong>Target:</strong> {selectedLocationData.targetValue}{selectedLocationData.unit}</div>
                      <div><strong>Unit:</strong> {selectedLocationData.unit}</div>
                    </div>
                  </div>
                )}

                <div className="space-y-2">
                  <Label htmlFor="value">Measured Value</Label>
                  <div className="flex space-x-2">
                    <Input
                      id="value"
                      type="number"
                      step="0.01"
                      placeholder="Enter measured value"
                      value={formData.value}
                      onChange={(e) => setFormData({...formData, value: e.target.value})}
                      required
                    />
                    {selectedLocationData && (
                      <Badge variant="outline" className="px-3 py-2">
                        {selectedLocationData.unit}
                      </Badge>
                    )}
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="operator">Operator</Label>
                  <Input
                    id="operator"
                    value={formData.operator}
                    onChange={(e) => setFormData({...formData, operator: e.target.value})}
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="notes">Notes (Optional)</Label>
                  <Textarea
                    id="notes"
                    placeholder="Additional notes or observations"
                    value={formData.notes}
                    onChange={(e) => setFormData({...formData, notes: e.target.value})}
                  />
                </div>

                <div className="flex justify-end space-x-2">
                  <Button type="button" variant="outline" onClick={onClose}>
                    Cancel
                  </Button>
                  <Button type="submit" disabled={isSubmitting}>
                    {isSubmitting ? 'Submitting...' : 'Submit Data'}
                  </Button>
                </div>
              </form>
            </TabsContent>

            {/* Barcode Scan Tab */}
            <TabsContent value="barcode" className="space-y-4">
              <div className="text-center py-8">
                <QrCode className="h-16 w-16 mx-auto mb-4 text-gray-400" />
                <h3 className="text-lg font-semibold mb-2">Barcode Scanner</h3>
                <p className="text-gray-600 mb-4">
                  Scan sample barcode to automatically input data
                </p>
                
                <Button onClick={startBarcodeScanner} className="mb-4">
                  <Camera className="h-4 w-4 mr-2" />
                  Start Camera Scanner
                </Button>
                
                <div className="text-left">
                  <form onSubmit={handleBarcodeSubmit} className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="barcode">Or Enter Barcode Manually</Label>
                      <Input
                        id="barcode"
                        placeholder="Scan or enter barcode"
                        value={barcodeInput}
                        onChange={(e) => setBarcodeInput(e.target.value)}
                        required
                      />
                    </div>
                    
                    <div className="flex justify-end space-x-2">
                      <Button type="button" variant="outline" onClick={onClose}>
                        Cancel
                      </Button>
                      <Button type="submit" disabled={isSubmitting || !barcodeInput.trim()}>
                        {isSubmitting ? 'Processing...' : 'Process Barcode'}
                      </Button>
                    </div>
                  </form>
                </div>
              </div>
            </TabsContent>

            {/* Document Upload Tab */}
            <TabsContent value="upload" className="space-y-4">
              <div className="text-center py-8">
                <FileText className="h-16 w-16 mx-auto mb-4 text-gray-400" />
                <h3 className="text-lg font-semibold mb-2">Document Upload</h3>
                <p className="text-gray-600 mb-4">
                  Upload Excel, CSV, or PDF documents containing sample data
                </p>
                
                <form onSubmit={handleFileUpload} className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="file">Select Document</Label>
                    <Input
                      id="file"
                      type="file"
                      accept=".xlsx,.xls,.csv,.pdf"
                      onChange={(e) => setUploadedFile(e.target.files?.[0] || null)}
                      required
                    />
                  </div>
                  
                  {uploadedFile && (
                    <div className="p-3 bg-green-50 rounded-lg text-left">
                      <p className="text-sm text-green-800">
                        <strong>Selected file:</strong> {uploadedFile.name}
                      </p>
                      <p className="text-xs text-green-600 mt-1">
                        File size: {(uploadedFile.size / 1024).toFixed(1)} KB
                      </p>
                    </div>
                  )}
                  
                  <div className="text-xs text-gray-500 text-left">
                    <p><strong>Supported formats:</strong></p>
                    <ul className="list-disc list-inside mt-1">
                      <li>Excel files (.xlsx, .xls)</li>
                      <li>CSV files (.csv)</li>
                      <li>PDF reports (.pdf)</li>
                    </ul>
                  </div>
                  
                  <div className="flex justify-end space-x-2">
                    <Button type="button" variant="outline" onClick={onClose}>
                      Cancel
                    </Button>
                    <Button type="submit" disabled={isSubmitting || !uploadedFile}>
                      {isSubmitting ? 'Processing...' : 'Upload & Process'}
                    </Button>
                  </div>
                </form>
              </div>
            </TabsContent>
          </Tabs>
        </CardContent>
      </Card>
    </div>
  );
}
